.car-img{
    max-width: 100%;
    max-height: 90%;
}

@media screen and (max-width: 768px) and (min-width: 426px){
  .car-img{
    max-width: 200px;
    max-height: 100px;
  }
}

@media screen and (max-width: 425px) {
  .car-img{
    max-width: 300px;
    max-height: 300px;
  }
}
